// Main imports
import { FC } from "react";
import { Link } from "react-router-dom";

// Redux Toolkit imports
import { useSelector } from "react-redux";
import { RootState, useAppDispatch } from "../redux/store";
import { addProduct, clearProducts, removeProduct } from "../redux/cart/slice";
import { ICartItem } from "../redux/cart/types";
import { useTranslation } from "react-i18next";

// Types
type CartItemsProps = {
  id: number;
  imageUrl: string;
  title: string;
  title_en: string;
  type: number;
  size: number;
  price: number;
  count: number;
};

// Main block
export const CartItem: FC<CartItemsProps> = ({
  id,
  imageUrl,
  title,
  title_en,
  type,
  size,
  price,
  count,
}) => {
  const dispatch = useAppDispatch();
  const { t, i18n } = useTranslation();

  const { priceUSD } = useSelector((state: RootState) => state.cartReducer);

  const typeNames = t("pizza.typeNames", { returnObjects: true }) as string[];

  return (
    <div className="cart__item">
      <div className="cart__item-img">
        <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
      </div>

      <Link to={`/pizza/${id}`} className="cart__item-info">
        <h3>{i18n.resolvedLanguage === "en-US" ? title_en : title}</h3>
        <p>
          {typeNames[type]}, {size} {t("pizza.size")}
        </p>
      </Link>

      <div className="cart__item-count">
        <button
          disabled={count === 1}
          className="button button--outline button--circle cart__item-count-minus"
          onClick={() =>
            dispatch(
              removeProduct({
                id,
                type,
                size,
                price,
              } as ICartItem)
            )
          }
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z"
              fill="#EB5A1E"
            />
            <path
              d="M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z"
              fill="#EB5A1E"
            />
          </svg>
        </button>

        <b>{count}</b>

        <button
          className="button button--outline button--circle cart__item-count-plus"
          onClick={() =>
            dispatch(
              addProduct({
                id,
                type,
                size,
                price,
                count,
                title,
                title_en,
                imageUrl,
              })
            )
          }
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z"
              fill="#EB5A1E"
            />
            <path
              d="M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z"
              fill="#EB5A1E"
            />
          </svg>
        </button>
      </div>

      <div className="cart__item-price">
        <b>
          {i18n.resolvedLanguage === "en-US"
            ? `${Math.ceil((price * count) / priceUSD)} $`
            : `${price * count} `}
        </b>
      </div>

      <div className="cart__item-remove">
        <button
          className="button button--outline button--circle"
          onClick={() => {
            if (window.confirm(t("cart.clearItemConfirm"))) {
              dispatch(
                clearProducts({
                  id,
                  type,
                  size,
                } as ICartItem)
              );
            }
          }}
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M5.92001 3.84V5.76V8.64C5.92001 9.17016 5.49017 9.6 4.96001 9.6C4.42985 9.6 4.00001 9.17016 4.00001 8.64L4 5.76L4.00001 3.84V0.96C4.00001 0.42984 4.42985 0 4.96001 0C5.49017 0 5.92001 0.42984 5.92001 0.96V3.84Z"
              fill="#EB5A1E"
            />
            <path
              d="M5.75998 5.92001L3.83998 5.92001L0.959977 5.92001C0.429817 5.92001 -2.29533e-05 5.49017 -2.29301e-05 4.96001C-2.2907e-05 4.42985 0.429817 4.00001 0.959977 4.00001L3.83998 4L5.75998 4.00001L8.63998 4.00001C9.17014 4.00001 9.59998 4.42985 9.59998 4.96001C9.59998 5.49017 9.17014 5.92001 8.63998 5.92001L5.75998 5.92001Z"
              fill="#EB5A1E"
            />
          </svg>
        </button>
      </div>
    </div>
  );
};
